$white: #fff;
$translucent: rgba(255, 255, 255, 0.5);
$green: #09606b;
$blue: #096099;

.container {
  max-width: 50rem;
}

a {
  color: $green;
}

article.real a {
  text-decoration-line: underline;
}

.sidebar {
  background-color: $green;
}

.sidebar a:focus {
  color: $translucent !important;
}

.sidebar a:hover {
  color: $translucent !important;
}

a.badge-category {
  color: $blue !important;
  background-color: $white !important;
  text-decoration-line: none !important;
}

a.badge-category:hover {
  color: $white !important;
  background-color: $blue !important;
  text-decoration-line: none !important;
}

a.badge-category:focus {
  color: $white !important;
  background-color: $blue !important;
  text-decoration-line: none !important;
}

a.badge-tag {
  color: $white !important;
  background-color: $green !important;
  text-decoration-line: none !important;
}

a.badge-tag:hover {
  color: $green !important;
  background-color: $white !important;
  text-decoration-line: none !important;
}

a.badge-tag:focus {
  color: $green !important;
  background-color: $white !important;
  text-decoration-line: none !important;
}

.toc-wrapper label {
  background: url(/img/menu-close.svg) no-repeat right center !important;
}

input#tocToggle:checked + label {
  background-image: url(/img/menu-open.svg) !important;
}

.toc {
  background: $green !important;
  color: $white !important;
}

a.toc {
  color: $white !important;
  text-decoration-line: none !important;
}

.toc-wrapper {
  background: $green !important;
  color: $white !important;
  text-decoration-line: none !important;
}

a.toc-link {
  color: $white !important;
  text-decoration-line: none !important;
}

ul.posts li {
  background: url('../img/list-bar.gif') no-repeat left top !important;
  list-style: none !important;
  padding: 0px 3px 0px 10px !important;
  margin: 0 !important;
}

ul.posts li:hover {
  background: url('../img/list-bar-active.gif') no-repeat left top !important;
  padding: 0px 3px 0px 13px !important;
  list-style: none !important;
  margin: 0 !important;
}

ul.posts li:active {
  background: url('../img/list-bar-active.gif') no-repeat left top !important;
  padding: 0px 3px 0px 13px !important;
  list-style: none !important;
  margin: 0 !important;
}

a.navigation-next {
  color: $green !important;
  border: 2px solid $green !important;
}

a.navigation-prev {
  color: $green !important;
  border: 2px solid $green !important;
}

.pagination-container {
  text-align: center;
}

ul.pagination li.page-item.active a {
  color: $green !important;
  border-bottom: 3px solid $green !important;
}

figcaption {
  padding: 0px !important;
  background-color: $white !important;
  text-align: center !important;
  margin-top: -15px;
}